'use strict';
import {educationModuleData} from "/data/education-module.js";

const e = React.createElement;

const EducationModules = () => {
  
  const RenderSliders = () => (
    educationModuleData.map((item) => (
          <div key={item.id} className="education-module-slide">
            <div className="grid-columns grid-2-columns container-center ">
              <div className="grid-item">
                  <span className="color-lightgrey">{item.subtitle}</span>
                  <h2 className="color-lightgrey">{item.title}</h2>
                    <p className="paragraph color-lightgrey">{item.content}</p>
                    <div className="grid-button grid-button--sm-2">
                      <a href={item.url}>
                        <div className="button button__large button--color-primary" role="button">
                          Watch this course
                        </div>
                      </a>
                      <a href={item.download}>
                        <div className="button button__large button--border-white" role="button">
                          Download pdf
                        </div>
                      </a>
                      </div>
                </div>
                <div className="grid-item">	
                    <div className="">
                    <figure className="img img--shrink-95">
                      <img src={require('/assets/mockups/devices/MacBook_Pro.svg')} alt="" />
                    </figure>
                  </div>
                </div>
              </div>
          </div>
    ))
  )


    return (
      <div className="education-module-sliders">
        <RenderSliders />
      </div>
    )
}

const domContainer = document.querySelector('#EducationModulesComponent');
ReactDOM.render(e(EducationModules), domContainer);	